@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap);
* {
    font-family: 'Roboto';    
}

.udp {
    background-color: #1f2833;
    width: 100%;
    min-height: 100%;
    top: 0;
    right: 0;
    display: flex;
    position: absolute;
    overflow-y: hidden;
}

.aaa {
    margin: 0 auto;
    padding: 0;
}

.topstreck {
    width: 18em;
    background-color: #1f2833;
    margin: auto;
    margin-top: 1em;
    height: 5em;
    line-height: 5em;
    vertical-align: middle;
    text-align: center;
}

